
import { defineComponent, reactive, onMounted } from 'vue';
import { Status, DevicePanic } from '@/models';
import config from '@/constants';
import router from '@/router';

export default defineComponent({
  name: 'PlantList',
  setup() {
    const statuses: Array<Status> = reactive([]);
    const panics: Array<DevicePanic> = reactive([]);

    onMounted(async () => {
      const token = sessionStorage.getItem('token') ?? undefined;
      if (token === undefined) {
        sessionStorage.removeItem('token');
        router.push({ path: '/login' });
        return;
      }

      (async () => {
        const response = await fetch(`${config.API_HOST}/v1/panic/index`, { headers: { Authorization: `Basic ${token}` } });

        if (response.status === 403) {
          sessionStorage.removeItem('token');
          router.push({ path: '/login' });
          return;
        }

        const newPanics: Array<DevicePanic> = await response.json();
        panics.push(...newPanics);
      })();

      (async () => {
        const response = await fetch(`${config.API_HOST}/v1/plant/index`, { headers: { Authorization: `Basic ${token}` } });

        if (response.status === 403) {
          sessionStorage.removeItem('token');
          router.push({ path: '/login' });
          return;
        }

        const newStatuses: Array<Status> = await response.json();
        statuses.push(...newStatuses);
      })();
    });

    const formatTime = (seconds: number): string => {
      if (seconds <= 0) {
        return '00h:00m';
      }

      if (!Number.isFinite(seconds)) {
        return 'Never';
      }

      const hours = Math.floor(seconds / 3600);
      const hoursString = `0${hours}`.slice(-2);

      const minutes = Math.floor((seconds / 60) % 60);
      const minutesString = `0${minutes}`.slice(-2);

      return `${hoursString}h:${minutesString}m`;
    };

    // eslint-disable-next-line class-methods-use-this
    const solveDevicePanic = async (id: number): Promise<void> => {
      const token = sessionStorage.getItem('token') ?? undefined;
      if (token === undefined) {
        sessionStorage.removeItem('token');
        router.push({ path: '/login' });
        return;
      }

      const response = await fetch(`${config.API_HOST}/v1/panic?id=${id}`, { method: 'DELETE', headers: { Authorization: `Basic ${token}` } });

      if (response.status === 403) {
        sessionStorage.removeItem('token');
        router.push({ path: '/login' });
        return;
      }

      router.go(0);
    };

    return {
      statuses, panics, solveDevicePanic, formatTime,
    };
  },
});
